import React, { useState, useEffect } from 'react';

const QuizComponent = ({ exercises }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [isAnswered, setIsAnswered] = useState(false);
  const [correctCount, setCorrectCount] = useState(0); // Track the number of correct answers

  // Sound arrays for correct and incorrect answers
  const soundDung = [
    '/static/sounds/anime-wow.mp3',
    '/static/sounds/woooooaah.mp3',
    '/static/sounds/wow.mp3',
  ];

  const soundSai = [
    '/static/sounds/babylaugh.mp3',
    '/static/sounds/bad.mp3',
    '/static/sounds/fail.mp3',
    '/static/sounds/laughing-man.mp3',
    '/static/sounds/whip.mp3',
  ];

  const [audio, setAudio] = useState(null);

  useEffect(() => {
    // Initialize Audio only in the client-side
    const currentAudio = new Audio(exercises[currentIndex].mp3);
    setAudio(currentAudio);

    // Cleanup audio when the component is unmounted or when currentIndex changes
    return () => {
      currentAudio.pause();
    };
  }, [currentIndex, exercises]);

  useEffect(() => {
    // Reset state when the question changes
    setSelectedAnswer(null);
    setIsAnswered(false);
  }, [currentIndex]);

  const handlePlayAudio = () => {
    if (audio) {
      audio.play();
    }
  };

  const handleAnswerSelection = (answer) => {
    setSelectedAnswer(answer);
    setIsAnswered(true);

    // Check if the selected answer is correct
    if (answer === exercises[currentIndex].correctAnswer) {
      setCorrectCount(correctCount + 1);
      // Play a random sound for correct answer
      const randomCorrectSound = soundDung[Math.floor(Math.random() * soundDung.length)];
      new Audio(randomCorrectSound).play();
    } else {
      // Play a random sound for incorrect answer
      const randomIncorrectSound = soundSai[Math.floor(Math.random() * soundSai.length)];
      new Audio(randomIncorrectSound).play();
    }
  };

  const handleNext = () => {
    if (currentIndex < exercises.length - 1) {
      setCurrentIndex(currentIndex + 1);
    }
  };

  const handlePrevious = () => {
    if (currentIndex > 0) {
      setCurrentIndex(currentIndex - 1);
    }
  };

  const { choices, correctAnswer } = exercises[currentIndex];
  return (
    <div style={{ padding: '20px', fontFamily: 'Arial, sans-serif', maxWidth: '600px', margin: 'auto' }}>
      <h2 style={{ color: '#007BFF', fontSize: '28px', textAlign: 'center' }}>Hãy chọn từ bạn nghe thấy</h2>
      <button
        onClick={handlePlayAudio}
        style={{
          padding: '12px 25px',
          backgroundColor: '#007BFF',
          color: '#fff',
          borderRadius: '10px',
          border: 'none',
          fontSize: '16px',
          cursor: 'pointer',
          display: 'block',
          margin: '20px auto',
        }}
      >
        ▶️ Phát âm
      </button>

      <div style={{ marginTop: '20px', textAlign: 'center' }}>
        {choices.map((choice, index) => (
          <button
            key={index}
            onClick={() => handleAnswerSelection(choice)}
            disabled={isAnswered}
            style={{
              padding: '12px 25px',
              margin: '10px',
              backgroundColor: selectedAnswer === choice ? (choice === correctAnswer ? 'green' : 'red') : '#f0f0f0',
              color: selectedAnswer === choice ? 'white' : 'black',
              borderRadius: '8px',
              cursor: 'pointer',
              fontSize: '16px',
              border: 'none',
            }}
          >
            {choice}
          </button>
        ))}
      </div>

      {isAnswered && (
        <div style={{ marginTop: '20px', textAlign: 'center' }}>
          <strong style={{ color: selectedAnswer === correctAnswer ? 'green' : 'red' }}>
            {selectedAnswer === correctAnswer ? 'Bạn đã chọn đúng!' : 'Bạn đã chọn sai!'}
          </strong>
        </div>
      )}

      <div style={{ marginTop: '20px', textAlign: 'center' }}>
        <strong>Câu trả lời đúng: {correctCount}</strong>
      </div>

      <div style={{ marginTop: '20px', textAlign: 'center' }}>
        <button
          onClick={handlePrevious}
          disabled={currentIndex === 0}
          style={{ padding: '10px 20px', marginRight: '10px', backgroundColor: '#ccc', borderRadius: '5px' }}
        >
          ◀️ Trước
        </button>
        <button
          onClick={handleNext}
          disabled={currentIndex === exercises.length - 1}
          style={{ padding: '10px 20px', backgroundColor: '#ccc', borderRadius: '5px' }}
        >
          Sau ▶️
        </button>
      </div>
    </div>
  );
};

export default function App() {
  const exercises = [
    { id: 1, mp3: '/static/phatam/01aip.mp3', choices: ['ip', 'ap', 'iêm', 'yêm'], correctAnswer: 'ip' },
    { id: 2, mp3: '/static/phatam/10ayeu.mp3', choices: ['yêu', 'ap', 'iêm', 'yêm'], correctAnswer: 'yêu' },
    { id: 3, mp3: '/static/phatam/015am.mp3', choices: ['âm', 'ap', 'iêm', 'yêm'], correctAnswer: 'âm' },
  ]

  return (
    <div>
      <QuizComponent exercises={exercises} />
    </div>
  );
}
